import {Box, useMediaQuery, useTheme} from "@mui/material";
import Header from "./client/LargeScreenView/Header";
import Services from "./client/LargeScreenView/Services";
import Video from "./client/LargeScreenView/Video";
import Mission from "./client/LargeScreenView/Mission";
import About from "./client/LargeScreenView/About";
import Vision from "./client/LargeScreenView/Vision";
import Solutions from "./client/LargeScreenView/Solutions";
import ServicesCard from "./client/LargeScreenView/servicesCard";
import Team from "./client/LargeScreenView/Team";
import Testimonials from "./client/LargeScreenView/Testimonials";
import Footer from "./client/LargeScreenView/Footer";
import Navbar from "./client/LargeScreenView/Navbar";
import React from "react";
import {createRoot} from "react-dom/client";
import './index.css';
import SMNavbar from "./client/SmallScreenView/SMNavbar";
import SMHeader from "./client/SmallScreenView/SMHeader";
import SMAbout from "./client/SmallScreenView/SMAbout";
import SMMission from "./client/SmallScreenView/SMMission";
import SMVision from "./client/SmallScreenView/SMVision";
import SMServices from "./client/SmallScreenView/SMServices";
import SMVideo from "./client/SmallScreenView/SMVideo";
import SMServicesCard from "./client/SmallScreenView/SMServicesCard";
import SMSolutions from "./client/SmallScreenView/SMSolutions";
import SMTeam from "./client/SmallScreenView/SMTeam";
import SMTestimonials from "./client/SmallScreenView/SMTestimonials";

const Index = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'));

  return (!isSmallScreen ? (
      <Box>
        <Navbar></Navbar>
        <Header></Header>
        <About></About>
        <Mission></Mission>
        <Vision></Vision>
        <Services></Services>
          <ServicesCard></ServicesCard>
          <Solutions></Solutions>
          <Team></Team>
          <Footer></Footer>
      </Box>) :
      (<Box>
          <SMNavbar/>
          <SMHeader/>
          <SMAbout/>
          <SMMission/>
          <SMVision/>
          <SMServices/>
          <SMServicesCard/>
          <SMSolutions/>
          <SMTeam/>
          <Footer></Footer>
      </Box>)
  );
}

createRoot(document.getElementById('root')).render(<Index />);
