import React from 'react';
import {AppBar, Box, Stack, Toolbar, Typography} from '@mui/material';
import {appbarStyle, buttonStyles, logoStyle, nameStyle, toolbarStyle} from "../stylesheets/navbarStyles";
import logo from '../assets/imgs/so_logo.jpeg';

const Navbar = () => {
    const scrollToSection = (id) => {
        const element = document.getElementById(id);
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
        }
    };

    const data = [
        {
            title: "Home",
            data: "home",
        },

        {
            title: "About",
            data: "about",
        },

        {
            title: "Services",
            data: "services",
        },

        {
            title: "Solutions",
            data: "solutions",
        },

        {
            title: "Team",
            data: "team",
        },
    ]

    return (
        <div>
            <AppBar position="static" sx={appbarStyle}>
                <Toolbar sx={toolbarStyle}>
                    <Stack direction={'row'} alignItems={'center'} columnGap={'1rem'}>
                        <img style={logoStyle} src={logo} alt={'logo'}/>
                        <Typography variant="h6" sx={nameStyle}>
                            Syed Saqlain Security Services LLC
                        </Typography>
                    </Stack>
                    <Box>
                        {data.map((Item) =>(<Box display={'inline'} sx={buttonStyles} onClick={() => scrollToSection(Item.data)}>
                            {Item.title}
                        </Box>))}
                    </Box>
                </Toolbar>
            </AppBar>
        </div>
    );
};

export default Navbar;
